import { BrowserRouter, Route, Switch } from "react-router-dom";
import Home from "./Home";
import "./styles.css";
import Catalog from "./Catalog";
import Welcome from "./Welcome";

export default function App() {
  return (
    <div className="app">
      <BrowserRouter>
        <Switch>
          <Route path="/catalog/:id">
            <Catalog />
          </Route>
          <Route path="/catalog">
            <Catalog />
          </Route>
          <Route path="/welcome">
            <Welcome />
          </Route>
          <Route>
            <Home />
          </Route>
        </Switch>
      </BrowserRouter>
    </div>
  );
}
